<template>
  <b-card>
    <div class="d-flex justify-content-between align-items-center mb-2">
      <flat-pickr
        v-model="rangeDate"
        class="form-control bg-white w-25"
        :config="{
          mode: 'range',
          wrap: true,
          dateFormat: 'm',
          wrap: true,
          altInput: true,
          altFormat: 'F',
          onChange: handleDateChange,
        }"
        placeholder="Range Date from - to"
      />
      <b-button variant="primary">
        <feather-icon icon="PlusIcon"></feather-icon> Milestone
      </b-button>
    </div>
    <VueDjGantt
      :from="from"
      :to="to"
      :list="list"
      :rows="rows"
      :items="items"
      @resized="onResize($event)"
      @moved="onMove($event)"
      @row="detailRow($event)"
      @item="detailItem($event)"
      @cell="detailCell($event)"
      locale="id"
    />
  </b-card>
</template>

<script>
import moment from "moment";
import axios from "axios";

export default {
  components: {},

  data() {
    return {
      list: [
        {
          id: "internalId",
          width: 80,
          header: {
            content: "# ID",
          },
        },
        {
          id: "name",
          width: 200,
          header: {
            content: "Title",
          },
        },
      ],

      from: +(+new Date()) - 2 * 24 * 60 * 60 * 1000,
      to: moment().startOf("day").add(1, "months"),

      rows: [],

      items: [],

      rangeDate: [],
    };
  },

  mounted() {
    this.get_data();
  },

  methods: {
    get_data() {
      axios({
        method: "get",
        url: "https://sw-api.hracademy.id/api/task",
        headers: {
          Authorization: "Bearer " + localStorage.getItem("sw_token"),
        },
      })
        .then((response) => {
          // foreach data from response
          response.data.result.data.forEach((element) => {
            // push data to rows
            this.rows.push({
              id: element.id,
              internalId: element.id,
              name: element.name,
            });

            // push data to items
            this.items.push({
              rowId: element.id,
              label: element.name,
              style: { background: "#24abf2" },
              time: {
                start: moment(element.start_date),
                end: moment(element.due_date).add(1, "days"),
              },
              resizable: true,
              moveable: true,
            });
          });
        })
        .catch((error) => {
          console.log(error);
        });
    },

    onResize(event) {
      let time = event.item.item.time.end;

      console.log(time);
    },

    onMove(event) {
      console.log(event);
    },

    handleDateChange() {
      let range = String(this.rangeDate)
        .split(" to ")
        .map((str) => parseInt(str));

      this.from = moment({
        year: moment().year(),
        month: range[0] - 1,
        day: 1,
      });
      this.to = moment({
        year: moment().year(),
        month: range[1],
        day: 1,
      });
    },

    detailRow(row) {
      console.log(row);
    },

    detailItem(item) {
      console.log(item);
    },

    detailCell(cell) {
      console.log(cell);
    },
  },
};
</script>